import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { IStep } from './step.interface';

@Component({
  selector: 'app-stepper',
  templateUrl: './stepper.component.html',
  styleUrls: ['./stepper.component.scss']
})
export class StepperComponent implements OnInit {

  @Input() steps: IStep[] = [];
  @Output('onchanged') onchanged: EventEmitter<IStep> = new EventEmitter<IStep>();

  constructor() { }

  ngOnInit(): void {
    this.setDefaultSelected();
  }

  setDefaultSelected(): void{
    if (this.steps.length === 0){
      return;
    }

    const currentActive = this.steps.find( x => x.active);
    if (!currentActive) {
      this.steps[0].active = true;
    }
  }

  select(step: IStep): void {
    if (!step.finished) {
      return;
    }

    this.steps.forEach( x => x.active = false);

    step.active = true;

    this.onchanged.emit(step);
  }
}
