import { createReducer, on } from '@ngrx/store';
import { addNode, setNode, removeNode, editNode } from '../actions/hierarchy.action';

export const initialState = [];

const _hierarchyReducer = createReducer(
  initialState,
  on(addNode, (state, payload) => {
    return [
      ...state,
      payload
    ];
  }),
  on(setNode, (state, payload) => {
    return payload.hierarchy;
  }),
  on(removeNode, (state, payload) => {
    return state.filter(item => item.id !== payload.id);
  }),
  on(editNode, (state, payload) => {
    return state.map(item => {
      if (item.id === payload.id) {
        return payload;
      }
      return item;
    });
  }),
);

export function hierarchyReducer(state, action) {
  return _hierarchyReducer(state, action);
}
