import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { RouteGuardGuard } from './guard/route-guard.guard';
import { StaffProvisioningGuard } from './guard/staff-provisioning.guard';

const routes: Routes = [
  {path: '', redirectTo: 'provisioning', pathMatch: 'full'},
  // {
  //   path: 'login',
  //   loadChildren: () => import('./modules/login/login.module').then(m => m.LoginModule)
  // },
  {
    path: 'provisioning',
    loadChildren: () => import('./modules/user-provisioning/user-provisioning.module').then(m => m.UserProvisioningModule),
    canActivate: [ RouteGuardGuard, StaffProvisioningGuard ]
  },
  {
    path: 'staffs',
    loadChildren: () => import('./modules/staff/staff.module').then(m => m.StaffModule),
    canActivate: [ RouteGuardGuard ]
  },
  {
    path: 'organization',
    loadChildren: () => import('./modules/organization/organization.module').then(m => m.OrganizationModule),
    canActivate: [ RouteGuardGuard ]
  },
  {
    path: 'applications',
    loadChildren: () => import('./modules/application/application.module').then(m => m.ApplicationModule),
    canActivate: [ RouteGuardGuard ]
  },
  {
    path: 'businessroles',
    loadChildren: () => import('./modules/business-role/business-role.module').then(m => m.BusinessRoleModule),
    canActivate: [ RouteGuardGuard ]
  },
  {
    path: 'jobduties',
    loadChildren: () => import('./modules/job-duty/job-duty.module').then(m => m.JobDutyModule),
    canActivate: [ RouteGuardGuard ]
  },
  {
    path: 'channels',
    loadChildren: () => import('./modules/channel/channel.module').then(m => m.ChannelModule),
    canActivate: [ RouteGuardGuard ]
  },
  {
    path: 'teams',
    loadChildren: () => import('./modules/team/team.module').then(m => m.TeamModule),
    canActivate: [ RouteGuardGuard ]
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
